import React, { Component } from "react";
import PropTypes from "prop-types";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";

const titleFormatter = (cell, row) => {
  return `<a href=/datas/routing/${row.id}/detail target="_blank">${cell}</a>`;
};

class RoutingList extends React.Component {
  constructor(props) {
    super(props);
    this.options = {
      sortIndicator: true,
      noDataText: "Khng c d liu"
    };
    this.selectRowProp = {
      mode: "radio",
      bgColor: "#c1f291",
      onSelect: props.handleRowSelect,
      clickToSelect: true,
      hideSelectColumn: true
    };
  }
  render() {
    return (
      <BootstrapTable
        data={this.props.routings}
        pagination={true}
        selectRow={this.selectRowProp}
        options={this.options}
        bordered={false}
        striped
        hover
        condensed
      >
        <TableHeaderColumn dataField="id" isKey hidden>
          #
        </TableHeaderColumn>

        <TableHeaderColumn dataField="code" dataFormat={titleFormatter}>
          M
        </TableHeaderColumn>

        <TableHeaderColumn dataField="name">Tn</TableHeaderColumn>
        <TableHeaderColumn dataField="note">Ghi ch</TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

RoutingList.propTypes = {
  routings: PropTypes.array.isRequired,
  handleRowSelect: PropTypes.func.isRequired
};

export default RoutingList;
