import React from "react";
import * as PropTypes from "prop-types";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import dateFormat from "../common/MyFormat";

function titleFormatter(cell, row) {
  return `<a href="/mrp/production/${row.id}/detail" > ${cell} </a>`;
}

function contactFormatter(cell) {
  if (!cell) return;
  return cell.code + cell.name;
}

function orderLineFormatter(cell) {
  if (!cell) return;
  return cell.spec;
}

class ProductionList extends React.Component {
  constructor(props) {
    super(props);

    this.options = {
      sortIndicator: true,
      noDataText: "No data"
    };

    this.selectRowProp = {
      mode: "radio",
      bgColor: "#c1f291",
      onSelect: props.handleRowSelect,
      clickToSelect: true,
      hideSelectColumn: true
    };
  }
  render() {
    return (
      <div>
        <div className="card">
          <BootstrapTable
            data={this.props.productions}
            pagination={true}
            selectRow={this.selectRowProp}
            options={this.options}
            bordered={false}
            striped
            hover
            condensed
          >
            <TableHeaderColumn dataField="id" isKey hidden>
              #
            </TableHeaderColumn>

            <TableHeaderColumn dataField="name" dataFormat={titleFormatter}>
              Tn
            </TableHeaderColumn>

            <TableHeaderColumn
              dataField="OrderLine"
              dataFormat={orderLineFormatter}
            >
              Sn phm
            </TableHeaderColumn>

            <TableHeaderColumn dataField="productDimension">
              Kch thc
            </TableHeaderColumn>
            <TableHeaderColumn dataField="productQty">
              S lng SX
            </TableHeaderColumn>
            <TableHeaderColumn dataField="productUom">VT</TableHeaderColumn>
            <TableHeaderColumn dataField="state">Trng thi</TableHeaderColumn>

            <TableHeaderColumn dataField="createdAt" dataFormat={dateFormat}>
              Ngy to
            </TableHeaderColumn>
          </BootstrapTable>
        </div>
      </div>
    );
  }
}

ProductionList.propTypes = {
  productions: PropTypes.array.isRequired,
  handleRowSelect: PropTypes.func.isRequired
};

export default ProductionList;
