import React from "react";
import PropTypes from "prop-types";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import dateFormat from "../common/MyFormat";

class ProductList extends React.Component {
  constructor(props) {
    super(props);

    this.options = {
      sortIndicator: true,
      noDataText: "No data"
    };

    this.selectRowProp = {
      mode: "radio",
      bgColor: "#c1f291",
      onSelect: props.handleRowSelect,
      clickToSelect: true,
      hideSelectColumn: true
    };
  }

  render() {
    return (
      <BootstrapTable
        data={this.props.products}
        selectRow={this.selectRowProp}
        options={this.options}
        version="4"
        bordered={false}
        striped
        hover
        condensed
        search
      >
        <TableHeaderColumn dataField="id" isKey hidden>
          Id
        </TableHeaderColumn>

        <TableHeaderColumn dataField="code">M SP</TableHeaderColumn>

        <TableHeaderColumn dataField="name">Tn sn phm</TableHeaderColumn>
        <TableHeaderColumn dataField="type">Loi</TableHeaderColumn>
        <TableHeaderColumn dataField="ProductCategory">
          Danh mc
        </TableHeaderColumn>

        <TableHeaderColumn dataField="listPrice">Gi</TableHeaderColumn>

        <TableHeaderColumn dataField="uom">VT</TableHeaderColumn>

        <TableHeaderColumn dataField="createdAt" dataFormat={dateFormat}>
          Ngy to
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

ProductList.propTypes = {
  products: PropTypes.array.isRequired,
  handleRowSelect: PropTypes.func.isRequired
};

export default ProductList;
